.Sidebar {
    width: 25%;
    height: 100vh;
    background-color: #efefef;
    border-left: 1px solid #ccc;
    padding: 1% 2%;
    box-sizing: border-box;
}
.user{     width: 20px;
    height: 20px;
    margin: 15px 25px 0px 0px;}
.coutwrap{ display: flex; justify-content: flex-end;}
.countbg{ background-color: #FCD561;
    border-radius: 17px;
    width: 45px;
    height: 45px;
    /* text-align: center; */
    /* position: relative; */
    /* top: 10px; */
    display: flex;
    align-items: center;
    justify-content: center;}
    .order{ font-size: 30px;
        text-transform: capitalize;
        font-weight: 600;
        margin: 20px 0px;}
    .icon{width: 30px; height: 30px;}
    .choosetime_bg{ background-color: #503E9D; font-size: 15px; 
        color: #fff; border-radius: 10px; padding: 20px; margin-bottom: 20px;}
        .disp{display: flex; justify-content: space-between;}
    .edit{ color:#FCD561;}
    .chosetime{display: flex; justify-content: space-between;color: #fff; margin-top: 20px; align-items: center;}
    .chosetime img {width: 30px;
    height: 30px;
    background-color: #604C96;
    border-radius: 12px;
    padding: 15px;
    margin-right: 12px; }
    .imgflex{ display: flex; align-items: center;}
    .orderlist{display: flex;
        justify-content: space-between;
        color: #000;
        font-size: 15px; margin-top: 20px;margin-top: 10px;
        align-items: center;}
    .orderlist img{    width: 60px;
        height: 35px;
        border-radius: 10px;}
        .amount{color: #5d5c5c; font-weight: 600;}
        .total{ display: flex;
            justify-content: space-between;
            margin-top: 40px;
            font-size: 18px;
            border-bottom: 2px solid #ccc;
            padding-bottom: 10px;}
        .totalbold{ font-weight: 600;}
        .checkoutwrap{ display: flex; justify-content: space-between; margin-top: 35px;}
        .person h1{ margin: 0px; padding: 0px; font-size: 16px; text-transform: capitalize; margin-bottom: 12px;}
        .inccountwrap{     border: 1px solid #ccc;
            display: flex;
            justify-content: space-between;
            padding: 6px 10px;
            font-size: 14px;
            width: 100%;
            border-radius: 5px;
            font-weight: 600;}
        .checkout{ background-color: #FCD561; position: absolute; right: 0px; padding: 15px 20px 15px;
            -webkit-border-top-left-radius: 10px;
            -webkit-border-bottom-left-radius: 10px;
            -moz-border-radius-topleft: 10px;
            -moz-border-radius-bottomleft: 10px;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;margin-top: 15px;
        }