.SectionWrapper {
    width: 100%;
    padding: 1% 8% 0;
    box-sizing: border-box;
}
.MenuSection span{
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;
    
}
.MenuSection .imgburger{ width: 30px;
    height: 30px;
    position: relative;
    left: 16px;
    top: 8px;}

    
     select {
        webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        outline: 0;
        box-shadow: none;
        border: 0!important;
        background-image: url();
        background-repeat: no-repeat;
        flex: 1 1;
        padding: 0 .5em;
        color: #fff;
        cursor: pointer;
        font-size: 1em;
        font-family: 'Open Sans', sans-serif;
        padding: 12px 37px;
        background: url(/static/media/img-arr-down.c6b335b6.png) right 15px top 20px no-repeat , url(/static/media/img-clock.cb4ab163.png) left 10px top 13px no-repeat,#FB7140;
        background-size: 8%, 12%;
        border-radius: 25px;
     }
     select::-ms-expand {
        display: none;
     }
     .select {
        position: relative;
        display: flex;
        width: 20em;
        height: 3em;
        line-height: 3;
        background: #5c6664;
        overflow: hidden;
        border-radius: .25em;
     }
     .select::after {
        content: '\25BC';
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 1em;
        background: #2b2e2e;
        cursor:pointer;
        pointer-events:none;
        transition:.25s all ease;
     }
     .select:hover::after {
        color: #23b499;
     }
     .headerrow{
         display: flex; justify-content: space-between; margin: 0px 0px 10px 0px;
     }
     .slide{    background-color: #e6e3e3;
        padding: 20px 12px;
        display: flex;
        justify-content: space-around;    border-radius: 20px;
    }
     .slide span{
        background-color: #fff;
        border-radius: 40px;
        width: 20%;
        position: relative;
        margin-right: 2%;
        text-align: center;
        
     }
     .slide span:hover{
        background-color: #FBD460;}

     .menuiconbg {    border: 1px solid #ccc;
        background-color: #fff;
        width: 50px;
        height: 50px;
        border-radius: 60px;
        position: relative;
        top: 15px;
        left: 14px;}
        .slide span:hover .menuiconbg {
            background-color: #f7f4f4;
            color: white;
            border: 1px solid #f7f4f4;
        }
        
     .menuiconbg img{ display: block;
        margin: 0 auto;
        position: relative;
        top: 20px;
        width: 15px;}
     .slide p{     margin: 25px 0px 25px 0px;
        font-size: 14px; font-weight: 400;}
        .pip{
            position: relative;
        top: 15px !important;
        width: 15px;
        }
        .arrow{
            width: 130px;
            background-color: #f1f1f1;
            height: 60px;
            border-radius: 20px;
            position: relative;
            top: 28px;
        }
        .arrow img{
            position: relative;
            top: 20px;
            left: 25px;
        }