.SectionWrapper {
    width: 100%;
    padding: 1% 8% 0;
    box-sizing: border-box;
}
.BannerSection {
    width: 100%;
    height: 140px;
    background-color: #FFF7ED;
    border-radius: 12px;
    position: relative;
    padding: 1px 50px;
    box-sizing: border-box;
}

.BannerIllustration {
    width: 150px;
    position: absolute;
    bottom: 0;
}

.BannerMetaWrapper {
    width: 60%;
    margin-left: auto;
}

.BannerHeading {
    color: #FB7443;
    margin-bottom: 0;
    font-size: 24px;
}

.BannerDescription {
    margin-top: 0;
    color: #999;
    font-size: 13px;
}

.CelebIcon {
    width: 32px;
    margin-left: 8px;
}

.LearnMoreText {
    color: #FB7443;
    font-size: 12px;
    text-align: right;
}

.IconRightOrange {
    margin-left: 8px;
    width: 14px;
}