.SectionWrapper {
    width: 100%;
    padding: 2% 8% 0;
    box-sizing: border-box;
}
.desertrow{ display: flex; justify-content: space-between; }
.dessertcolum{position: relative;}
.dessertcolum img{
    width: 100%; border-radius: 12px; 
}
.desertrow span{position: absolute; bottom: 63px; left: 0px; background-color: #ccc;
    -webkit-border-top-right-radius: 10px;
    -webkit-border-bottom-left-radius: 10px;
    -moz-border-radius-topright: 10px;
    -moz-border-radius-bottomleft: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px; padding: 10px 15px;} 
    .subtxt{ font-size: 14px; margin-top: 12px;}
